.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
  }
  
  h1 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .loading {
    font-size: 1.5em;
    margin: 20px 0;
  }
  
  .historical-weather-info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .historical-item {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    margin: 10px 0;
    width: 100%;
    max-width: 600px;
    text-align: left;
  }
  
  .historical-item p {
    margin: 5px 0;
    font-size: 1em;
  }
  
  .historical-item p:first-child {
    font-weight: bold;
    font-size: 1.1em;
  }
  